<style scoped>
  .detailtitle {
    line-height: 24px;
    border-left: 5px solid #2d8cf0;
    font-weight: bold;
    font-size: 16px;
    text-indent: 10px;
  }

  .detail_li .itemli {
    line-height: 40px;
    font-size: 14px;
  }

  .ivu-row {
    padding: 10px;
  }

  .detail_li .demo-badge-alone {
    height: 18px !important;
    line-height: 16px !important;
    padding: 0 4px !important;
    font-size: 12px !important;
  }

  .detail_tablebox {
    width: 100%;
    height: auto;
  }

  .setupload {
    /* width: 50px; */
    height: 30px;
    border: none;
    float: left;
    line-height: 1;
    margin-right: 5px;
  }

    .setupload .ivu-btn {
      padding: 2px 2px 2px;
      font-size: 12px;
    }

    .setupload .ivu-upload .ivu-upload-drag {
      border: 0px !important;
    }

  .successbtn {
    text-align: center;
  }

  .detail_title1 {
    display: inline-block;
    width: 80px;
    /* font-weight: bold; */
  }

  .detail_title2 {
    display: inline-block;
    width: 78px;
  }

  .detail_title3 {
    display: inline-block;
    width: 100px;
  }

  .detail_tablebox > > > .ivu-table-cell {
    padding-left: 5px;
    padding-right: 5px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: normal;
    word-break: break-all;
    box-sizing: border-box;
  }

  .ivu-table .demo-table-info-row td {
    display: none;
  }

  .Mustfill {
    color: red;
  }

  .linkurlcolor {
    color: #2d8cf0;
  }

  .Filesbox:hover {
    cursor: pointer;
  }
</style>
<template>
  <div>
    <div style="width:100%;">
      <div class="itembox">
        <p class="detailtitle"></p>
        <!-- <p>{{getids}}</p> -->
        <div style="width:100%;min-height:200px;background:#f5f7f9;margin:15px 0">
          <Row v-if="waybillinfo!=null">
            <Col style="width: 23%;float: left;">
            <ul class="detail_li detail1">
              <li class="itemli">
                <span class="detail_title1">ID</span>
                <span>{{waybillinfo.ID}}</span>
              </li>
              <li class="itemli">
                <span class="detail_title1">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span>
                  {{waybillinfo.ExcuteStatusName}}
                  <Button v-if="waybillinfo.ID!=null" icon='md-calendar' type="success" size='small' @click="logchange"></Button>
                </span>
              </li>
              <li class="itemli">
                <span class="detail_title1"></span>
                <span>{{waybillinfo.SourceName}}</span>
              </li>
              <li class="itemli">
                <span class="detail_title1"></span>
                <span>{{waybillinfo.Merchandiser}}</span>
              </li>

              <li class="itemli">
                <span class="detail_title1">&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span style="color:'#ccc';font-width:600">{{waybillinfo.chgTotalCurrency}}  {{waybillinfo.chgTotalPrice}}</span>
              </li>
              <li class="itemli">
                <span class="detail_title1"></span>
                <span style="color:'#ccc';font-width:600" v-if="waybillinfo.overDue==true"></span>
                <span style="color:'#ccc';font-width:600" v-else></span>
              </li>
              <li class="itemli" v-if="waybillinfo.Condition!=undefined">
                <Icon type="md-alert" style="font-size: 22px;color: #da2828;" />
                <Tag color="geekblue" v-if="waybillinfo.Condition.AgencyCheck==true"></Tag>
                <Tag color="purple" v-if="waybillinfo.Condition.AgencyPayment==true"></Tag>
                <Tag color="blue" v-if="waybillinfo.Condition.AgencyReceive==true"></Tag>
                <Tag color="cyan" v-if="waybillinfo.Condition.ChangePackaging==true"></Tag>
                <Tag color="green" v-if="waybillinfo.Condition.LableServices==true"></Tag>
                <Tag color="gold" v-if="waybillinfo.Condition.PayForFreight==true"></Tag>
                <Tag color="orange" v-if="waybillinfo.Condition.Repackaging==true"></Tag>
                <Tag color="volcano" v-if="waybillinfo.Condition.UnBoxed==true"></Tag>
                <Tag color="red" v-if="waybillinfo.Condition.VacuumPackaging==true"></Tag>
                <Tag color="magenta" v-if="waybillinfo.Condition.WaterproofPackaging==true"></Tag>
                <Tag color="lime" v-if="waybillinfo.Condition.QualityInspection==true"></Tag>
                <Tag color="yellow" v-if="waybillinfo.Condition.Unboxing==true"></Tag>
              </li>
            </ul>
            </Col>
            <Col style="width: 27%;float: left;">
            <ul class="detail_li">
              <li class="itemli">
                <span class="detail_title2"></span>
                <span>{{waybillinfo.CreateDate|showDateexact}}</span>
              </li>
              <li class="itemli">
                <span class="detail_title2" style="float: left;">&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span style="display: inline-block; width:70%; line-height: 1;">
                  <p v-for="(item,index) in waybillinfo.Supplier">{{item.Supplier}}&nbsp;&nbsp;<Tag color="volcano">LV {{item.SupplierGrade}}</Tag></p>
                </span>
              </li>
              <li class="itemli">
                <span class="detail_title2"></span>
                <span v-if="warehouseID.indexOf('SZ')!=-1||(waybillinfo.Type==1||waybillinfo.Type==2) ">{{waybillinfo.TypeName}}</span>
                <Select v-else
                        v-model="waybillinfo.Type"
                        style="width:40%"
                        @on-change='changewaybillType'>
                  <Option v-for="(item,index) in TypeArr"
                          :value="item.value"
                          :key="index">
                    {{ item.label}}
                  </Option>
                </Select>
              </li>
              <li class="itemli">
                <span class="detail_title2">&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span style="color:'#ccc';font-width:600">CNY {{waybillinfo.total}}</span>
              </li>
              <li class="itemli">
                <span class="detail_title2">&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span style="color:'#ccc';font-width:600">CNY {{waybillinfo.totalDebt}}</span>
              </li>
              <li class="itemli">
                <span class="detail_title2"></span>
                <span style="line-height: 20px;width: 70%;display: inline-block;">
                  <div v-for="(item,index) in waybillinfo.LableFile">
                    <a class="linkurlcolor Filesbox" @click="clackFilesProcess(item.Url)">{{item.CustomName}}</a>
                    <a @click="fileprint(item.Url)"></a>
                  </div>
                </span>
              </li>
              <li class="itemli" v-if="waybillinfo.Type==2">
                <span class="detail_title2" style="float: left;"></span>
                <span style="display: inline-block;">
                  <div v-for="(item,index) in waybillinfo.Files" v-if="item.Type==25">
                    <span class="linkurlcolor Filesbox" @click="clackFilesProcess(item.Url)">{{item.CustomName}}</span>
                    <a @click="fileprint(item.Url)"></a>
                  </div>
                </span>
              </li>
            </ul>
            </Col>
            <Col style="width: 30%;float: left;">
            <ul class="detail_li">
              <li class="itemli">
                <span class="detail_title3">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span>{{waybillinfo.EnterCode}}{{waybillinfo.ClientName}}<Tag color="geekblue">LV {{clientGrade}}</Tag></span>
              </li>
              <li class="itemli" v-if="waybillinfo.Type==1">
                <span class="detail_title3"></span>
                <span>{{waybillinfo. AppointTime|showDateexact}}</span>
              </li>
              <li class="itemli" v-if="waybillinfo.Type==1">
                <span class="detail_title3">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span>{{waybillinfo.coeContact}}</span>
              </li>
              <li class="itemli" v-if="waybillinfo.Type==1">
                <span class="detail_title3"></span>
                <span>{{waybillinfo.IDTypeName}}{{waybillinfo.IDNumber}}</span>
              </li>
              <li class="itemli" v-if="waybillinfo.Type==1">
                <span class="detail_title3"></span>
                <span>{{waybillinfo.coePhone}}</span>
              </li>

              <li class="itemli" v-if="waybillinfo.Type!=1">
                <span class="detail_title3">()</span>
                <span>
                  <Select v-model="waybillinfo.CarrierID"
                          style="width:60%"
                          @on-change="changeacrrier"
                          :label-in-value="true"
                          :disabled='true'>
                    <Option v-for="item in CarrierList"
                            :value="item.ID"
                            :key="item.ID">
                      {{ item.Name }}
                    </Option>
                  </Select>
                  <Icon type="md-create" v-if="warehouseID.indexOf('SZ')!=-1&&waybillinfo.Type==3" @click="showchangeexpress=true" />
                </span>
              </li>
              <li class="itemli" v-if="waybillinfo.Type!=1">
                <span class="detail_title3">()</span>
                <span>
                  <Input style="width:60%" v-model="waybillinfo.Code" :disabled='true' />
                </span>
                <Icon type="md-create" v-if="warehouseID.indexOf('SZ')!=-1&&waybillinfo.Type==3" @click="clickshowcode" />
              </li>
              <li class="itemli" v-if="waybillinfo.Type==2">
                <span class="detail_title3">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span>
                  <Input v-model="waybillinfo.Driver" :disabled='true' style="width:60%" />
                </span>
              </li>
              <li class="itemli" v-if="waybillinfo.Type==2">
                <span class="detail_title3">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span>
                  <Input v-model="waybillinfo.CarNumberID" :disabled='true' style="width:60%" placeholder="" />
                </span>
              </li>

              <li class="itemli" v-if="waybillinfo.Type!=1">
                <span class="detail_title3" style="float: left;">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span style="display: inline-block;max-width:60%;line-height: 20px;">
                  <i v-if="waybillinfo!=null"> {{waybillinfo.Receiver.Name}}</i>
                </span>
                <Icon type="md-create" v-if="warehouseID.indexOf('SZ')!=-1&&waybillinfo.Type==3" @click="clickshoReceiver" />
              </li>
              <li class="itemli" v-if="waybillinfo.Type!=1">
                <span class="detail_title3" style="float: left;">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span style="display: inline-block;max-width:60%;line-height: 20px;">
                  <i v-if="waybillinfo!=null"> {{waybillinfo.Receiver.Mobile}}</i>
                </span>
                <Icon type="md-create" v-if="warehouseID.indexOf('SZ')!=-1&&waybillinfo.Type==3" @click="clickshoReceiver" />
              </li>

              <li class="itemli" v-if="waybillinfo.Type!=1">
                <span class="detail_title3" style="float: left;">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span style="display: inline-block;max-width:60%;line-height: 20px;">
                  <i v-if="waybillinfo!=null">{{waybillinfo.CoeAddress}}</i>
                </span>
                <Icon type="md-create" v-if="warehouseID.indexOf('SZ')!=-1&&waybillinfo.Type==3" @click="clickshoReceiver" />
              </li>
              <li class="itemli" v-if="waybillinfo.Type!=2&&warehouseID.indexOf('SZ')==-1">
                <span class="detail_title3">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <div v-for="(item,index) in waybillinfo.Files" style="display: inline-block;" v-if="item.Type==25">
                  <!-- <span>{{item.CustomName}}</span> -->
                  <span class="linkurlcolor Filesbox" @click="clackFilesProcess(item.Url)">{{item.CustomName}}</span>
                  <a @click="fileprint(item.Url)"></a>
                </div>
              </li>
            </ul>
            </Col>
            <Col style="width: 20%;float: left;">
            <ul class="detail_li" style="margin-left:20px;">
              <li v-if="warehouseID.indexOf('SZ')!=-1" class="itemli">{{waybillinfo.LotNumber}}</li>
              <li class="itemli" v-if="waybillinfo.Type==3&&warehouseID.indexOf('SZ')!=-1">
                <span class="detail_title3">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <div v-for="(item,index) in waybillinfo.Files" style="display: inline-block;" v-if="item.Type==25">
                  <!-- <span>{{item.CustomName}}</span> -->
                  <span class="linkurlcolor Filesbox" @click="clackFilesProcess(item.Url)">{{item.CustomName}}</span>
                  <a @click="fileprint(item.Url)"></a>
                </div>
              </li>
              <li class="itemli">
                <span style="float:left;line-height: 27px;" v-if="waybillinfo.ExcuteStatus!=215"></span>
                <div v-if="waybillinfo.ExcuteStatus!=215">
                  <div class="setupload">
                    <Button type="primary"
                            icon="ios-cloud-upload"
                            @click="SeletUpload('Waybill')">
                      
                    </Button>
                  </div>
                  <div class="setupload">
                    <Button type="primary"
                            icon="md-reverse-camera"
                            @click="fromphotos('Waybill')">
                      
                    </Button>
                  </div>
                </div>

              </li>
              <li class="itemli" v-if="IsSendGoodsFile==true" style="padding-top:10px;clear: both;">
                <span style="float:left;line-height: 27px;">&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <div class="setupload">
                  <Button type="primary"
                          size="small"
                          icon="ios-cloud-upload"
                          @click="SeletUpload('Clientimg')">
                    
                  </Button>
                </div>
                <div class="setupload">
                  <Button type="primary"
                          size="small"
                          icon="md-reverse-camera"
                          @click="fromphotos('Clientimg')">
                    
                  </Button>
                </div>
              </li>
              <li style="clear: both;">
                <p></p>
                <div v-for="(item,index) in waybillinfo.FeliverGoodFile">
                  <div v-if="item.Type==8010" class="Filesbox">
                    <span class='linkurlcolor' @click="clackFilesProcess(item.Url)">{{item.CustomName}}</span>
                    <Tooltip content="" placement="top">
                      <Icon type="ios-trash-outline" @click.native="handleRemove(item,'Waybill')"></Icon>
                    </Tooltip>
                    <Tooltip content="" placement="top">
                      <Icon type="ios-print-outline" @click="fileprint(item.Url)" />
                    </Tooltip>
                  </div>
                </div>
                <p v-if="IsSendGoodsFile==true"></p>
                <div v-if="IsSendGoodsFile==true">
                  <div v-for="(item,index) in waybillinfo.SendGoodsFile">
                    <div v-if="item.Type==8020" class="Filesbox">
                      <span class='linkurlcolor' @click="clackFilesProcess(item.Url)">{{item.CustomName}}</span>
                      <Tooltip content="" placement="top">
                        <Icon type="ios-trash-outline" @click.native="handleRemove(item,'Clientimg')"></Icon>
                      </Tooltip>
                      <Tooltip content="" placement="top">
                        <Icon type="ios-print-outline" @click="fileprint(item.Url)" />
                      </Tooltip>
                    </div>
                  </div>
                </div>
              </li>
            </ul>
            </Col>
          </Row>
        </div>
      </div>
      <div class="itembox">
        <p class="detailtitle"></p>
        <div style="margin:15px 0">
          <ButtonGroup style="width:28%">
            <Input v-model.trim="searchkey"
                   placeholder=""
                   clearable
                   @on-clear='search_pro'
                   style="width:80%;float:left;position: relative;left: 3px" />
            <Button style="float:left" @click="search_pro" type="primary"></Button>
            <!-- <Button style="float:left" type="primary"></Button> -->
          </ButtonGroup>
          <!-- <Button type="primary" @click="Labelprint"></Button> -->
          <Button type="primary" @click="clickshowchangebox" v-if='warehouseID.indexOf("SZ")==-1' :disabled='waybillinfo.ExcuteStatus==215?true:false'></Button>
          <Button type="primary" @click="showprint=true"></Button>
          <Button type="primary" @click="showBudget('meet','in',waybillinfo.Source)"></Button>
          <Button type="primary" @click="showBudget('meet','out',waybillinfo.Source)"></Button>
          <div style="float:right">
            <Button type="primary"
                    shape="circle"
                    icon="md-checkmark"
                    @click="finish_btn"
                    :disabled="isdisabled==true?true:false">
              
            </Button>
            <Button v-if='warehouseID.indexOf("HK")==-1'
                    :disabled="isdisabled==true?true:false"
                    type="warning"
                    shape="circle"
                    icon="ios-alert-outline"
                    @click="isAbnormal=true">
              
            </Button>
          </div>
        </div>
        <div>
          <div class="detail_tablebox">
            <Table ref="selection"
                   :columns="tabletitle"
                   :data="detailitem"
                   :loading=loading
                   @on-selection-change="handleSelectRow">
              <template slot-scope="{ row, index }" slot="PartNumber">
                <div style="display: flex;justify-content: space-between;align-items: center;">
                  <div style="width:120px;overflow: hidden;">
                    <span>{{row.Product.PartNumber}}</span>
                  </div>
                  <ul style="float:right;" v-if="row.Conditions!=null">
                    <li><Tag v-if="row.Conditions.IsCIQ==true" color="primary"></Tag></li>
                    <li><Tag v-if="row.Conditions.IsCCC==true" color="warning">CCC</Tag></li>
                    <li><Tag v-if="row.Conditions.IsEmbargo==true" color="error"></Tag></li>
                    <li><Tag v-if="row.Conditions.IsHighPrice==true" color="magenta"></Tag></li>
                  </ul>
                </div>

              </template>
              <template slot-scope="{row,index}" slot="Boxing_code">
                <span v-if='warehouseID.indexOf("SZ")!=-1'>{{row.BoxCode|showboxcode}}</span>
                <div style="display:inline-block;width:80%" v-else>
                  <Select v-model="row.BoxCode"
                          :transfer="true"
                          filterable
                          allow-create
                          :disabled='row.CurrentQuantity<=0?true:false'
                          @on-create="handleCreate1($event,index)"
                          @on-change="clickDropdown($event,index)">
                    <Option v-for="(item,index) in boxingarr"
                            :value="item.Code"
                            :key="index"
                            :label="item.Code">
                      <span>{{item.Code}}</span>
                    </Option>
                  </Select>
                </div>
              </template>
              <template slot-scope="{ row, index }" slot="imglist">
                <p v-for="item in row.Imagefiles" class="Filesbox">
                  <span class='linkurlcolor' @click="clackFilesProcess(item.Url)">{{item.CustomName}}</span>
                  <Icon type="ios-trash-outline"
                        :ref="row.ID"
                        @click.native="handleRemove(item,row)"></Icon>
                </p>
              </template>
              <template slot-scope="{ row, index }" slot="operation">
                <div class="setupload">
                  <Button type="primary"
                          size="small"
                          icon="ios-cloud-upload"
                          @click="SeletUpload(row)"
                          :disabled='row._disabled==true?true:false'>
                    
                  </Button>
                </div>
                <div class="setupload">
                  <Button type="primary"
                          size="small"
                          icon="md-reverse-camera"
                          @click="fromphotos(row)"
                          :disabled='row._disabled==true?true:false'>
                    
                  </Button>
                </div>
              </template>
            </Table>
            <div class="successbtn" style="margin-top:20px;">
              <Button type="primary"
                      icon="md-checkmark"
                      @click="finish_btn"
                      :disabled="isdisabled==true?true:false">
                
              </Button>
              <Button v-if='warehouseID.indexOf("HK")==-1'
                      type="warning"
                      icon="ios-alert-outline"
                      @click="isAbnormal=true"
                      :disabled="isdisabled==true?true:false">
                
              </Button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--   -->
    <Modal v-model="Budgetdetail"
           width="55%"
           :closable="false"
           :mask-closable="false"
           :footer-hide="true">
      <div style="position: absolute;right:20px;z-index:99999;width:30px">
        <Icon type="ios-close" style="float:right;font-size:30px;font-weight:bold;" @click="closeBudget" />
      </div>
      <div>
        <router-view></router-view>
      </div>
    </Modal>
    <!--  -->
    <!--   -->
    <Modal v-model="showchangebox"
           title=""
           @on-ok="ok_changebox"
           @on-cancel="cancel">
      <Select v-model="newboxcode">
        <Option v-for="item in boxingarr" :value="item.Code" :key="item.ID">{{ item.Code }}</Option>
      </Select>
    </Modal>
    <!--   -->
    <!--   -->
    <div v-if="WarehousingMsg==true">
      <Modal v-model="WarehousingMsg"
             title=""
             @on-ok="ok_Warehousing"
             @on-cancel="cancel_Warehousing">
        <div>
          <span></span>
        </div>
      </Modal>
    </div>
    <!--   -->
    <!--   -->
    <Modal v-model="isAbnormal" title="" @on-visible-change="errorstatue">
      <div slot="close">
        <Icon type="md-close"
              color="rgb(33, 28, 28)"
              @click="closeerror"
              style="font-size:18px;" />
      </div>
      <span style="line-height: 1; display: block; padding-bottom: 10px;">
        <em class="Mustfill">*</em>
      </span>
      <Input v-model="Summary" type="textarea" :rows="2" placeholder="" />
      <div slot="footer">
        <Button @click="closeerror"></Button>
        <Button type="primary" @click="Abnormal_btn"></Button>
      </div>
    </Modal>
    <!--  -->
    <!--   -->
    <Modal v-model="showprint"
           title="">
      <p slot="header" style="font-size:16px">
        <span></span>
        <!-- <Icon style="font-size:20px" type="ios-desktop-outline" /> -->
      </p>
      <CheckboxGroup v-model="fruit">
        <Checkbox label=""></Checkbox>
        <Checkbox label=""></Checkbox>
        <Checkbox v-if="Iswaybillprint==true" label=""></Checkbox>
      </CheckboxGroup>
      <Modal v-model="preview" fullscreen title="Fullscreen Modal">
        <div>This is a fullscreen modal</div>
      </Modal>
      <div slot='footer'>
        <Button @click="cancel"></Button>
        <!-- <Button @click="preview_btn" type="primary"></Button> -->
        <!-- <Button @click="preview_btn" type="info"></Button> -->
        <Button @click="CgAllprint" type="primary"></Button>
      </div>
    </Modal>
    <!--   -->
    <!--  -->
    <Modal v-model="showlogged"
           :footer-hide='true'
           :mask-closable='false'
           width='60'>
      <div slot="close">
        <Icon style="font-size:21px;color:#cccccc;padding-top: 5px" type="ios-close-circle-outline" />
      </div>
      <div slot="header">
        <span style="font-size:18px;color:#1aaff7;"></span>
      </div>
      <logg-ed ref="logged" :key='loggdetime' v-if="showlogged" :WaybillID='waybillinfo.ID'></logg-ed>
    </Modal>
    <!--  -->
    <!--   -->
    <Modal :mask-closable='false'
           v-model="showchangeexpress"
           title="">
      <!--<Express ref='Express' v-if="showchangeexpress" :WaybillID="waybillinfo.ID"  :uploadwaybill='uploadwaybill'></Express>-->
      <Express ref='Express' v-if="showchangeexpress" :WaybillID="waybillinfo.ID" :ShipperCode="waybillinfo.CarrierName=='EMS'?'EMS':(waybillinfo.CarrierName==''? 'SF' : 'KYSY')" :Expresstype=waybillinfo.Extype :PayType=waybillinfo.ExPayType :ThirdPartyCardNo="waybillinfo.ThirdPartyCardNo" :uploadwaybill='uploadwaybill'></Express>
      <div slot="footer">
        <Button @click="cancel"></Button>
        <Button @click="ok_changeexpress" type="primary"></Button>
      </div>
    </Modal>
    <!--   -->
    <Modal :mask-closable='false'
           v-model="showchangewaybillcode"
           title="">
      <Input v-model.trim="changewaybillcodeinput" placeholder="" />
      <div slot="footer">
        <Button @click="cancel_changecode"></Button>
        <Button @click="ok_changecode" type="primary"></Button>
      </div>
    </Modal>
    <Modal :mask-closable='false'
           v-model="showchangeReceiver"
           title="">
      <div>
