              res.Notices[i]._disabled = true
            } else {
              res.Notices[i]._disabled = false
            }
          }
          this.detailitem = res.Notices
          this.getboxarr()
          this.Carriers(res.Waybill.Type);
          this.GetDriversCars(res.Waybill.CarrierID)
          this.loading = false;
          // this.fruit=['','','']
