          var Type = null
          if (type == "Waybill") {
            Type = 8010
          } else {
            Type = 8020
          }
          var data = {
            SessionID: type,
            AdminID: sessionStorage.getItem("userID"),
            Data: {
              WayBillID: this.waybillinfo.ID,
              WsOrderID: this.waybillinfo.OrderID,
              Type: Type
            }
          };
          FormPhoto(data);
        } else {
          var data = {
            SessionID: type.ID,
