            CarNumber1: this.waybillinfo.CarNumberID,
            Files: this.waybillinfo.FeliverGoodFile,
            LoadingExcuteStatus: this.waybillinfo.LoadingExcuteStatus, // d:\projects_vs2015\yahv\solutions\yahv.underly\enums\enum.cgexcutestatus.cs
            WarehouseID: this.warehouseID,//ID
            Code: this.waybillinfo.Code
          },
